import type { RigidBody } from '@dimforge/rapier2d'
import {
  MutableRefObject,
  RefObject,
  useCallback,
  useEffect,
  useRef,
} from 'react'
import { coords } from '../lib/coords'
import { inBoundsObject, px } from '../lib/utils'
import { useMachine } from './MachineContext'
import { useLoopHandler } from './PhysicsContext'

export function getPositionStyles(x: number, y: number, angle: number = 0) {
  return {
    position: 'absolute' as const,
    top: 0,
    left: 0,
    transform: `translate(${px(x)}, ${px(y)}) translate(-50%, -50%) rotate(${angle}rad)`,
  }
}

export function usePositionedBodyRef<T extends HTMLElement>(
  bodyRef: RefObject<RigidBody>,
  {
    width,
    height,
    xBasis = 0,
    yBasis = 0,
  }: {
    width: number
    height: number
    xBasis?: number
    yBasis?: number
  },
): MutableRefObject<T | null> {
  const elRef = useRef<T>(null)
  const { viewBoundsRef } = useMachine()

  const wasVisibleRef = useRef(true)

  const updateStyle = useCallback(() => {
    const { current: el } = elRef
    const { current: body } = bodyRef

    if (!el) {
      return
    }

    if (!body) {
      el.style.display = 'none'
      wasVisibleRef.current = false
      return
    }

    const [bodyX, bodyY] = coords.fromBody.vector(body)

    const isVisible = inBoundsObject(
      bodyX,
      bodyY,
      width,
      height,
      viewBoundsRef.current,
    )
    if (isVisible != wasVisibleRef.current) {
      el.style.display = isVisible ? 'block' : 'none'
    }
    if (isVisible) {
      Object.assign(
        el.style,
        getPositionStyles(
          bodyX - xBasis,
          bodyY - yBasis,
          coords.fromBody.angle(body),
        ),
      )
    }
    wasVisibleRef.current = isVisible
  }, [xBasis, yBasis, bodyRef, height, viewBoundsRef, width])

  useEffect(updateStyle)
  useLoopHandler(updateStyle, [updateStyle])

  return elRef
}
