import { coords } from '../../lib/coords'
import { Sized, Vector } from '../../types'
import { useRigidBody } from '../MachineTileContext'
import { EditableWidget, useSelectHandlers } from '../MachineTileEditor'
import { useRapierEffect } from '../PhysicsContext'
import { getPositionStyles, usePositionedBodyRef } from '../positionStyles'

export interface SpinnerWidget extends Vector, Sized {
  type: 'spinner'
  mass?: number
  restitution?: number
  damping?: number
}

export function SpinnerPreview() {
  return (
    <div
      style={{
        background: 'black',
        width: '50%',
        height: '10%',
      }}
    ></div>
  )
}

export default function Spinner({
  id,
  onSelect,
  x,
  y,
  width,
  mass = 2,
  restitution = 0.75,
  damping = 0.5,
}: SpinnerWidget & EditableWidget) {
  const height = 10

  const bodyRef = useRigidBody(
    ({ RigidBodyDesc, ColliderDesc, RigidBodyType }) => {
      return {
        key: id,
        bodyDesc: new RigidBodyDesc(RigidBodyType.Dynamic)
          .lockTranslations()
          .setAngularDamping(damping),
        colliderDescs: [
          ColliderDesc.cuboid(0, 0).setMass(mass).setRestitution(restitution),
        ],
      }
    },
    [damping, id, mass, restitution],
  )

  // Update position and size without recreating so angular momentum is preserved during edits
  useRapierEffect(() => {
    const { current: body } = bodyRef
    if (!body) {
      return
    }
    body.setTranslation(coords.toRapier.vectorObject(x, y), true)
    body.collider(0).setHalfExtents({
      x: coords.toRapier.length(width / 2),
      y: coords.toRapier.length(height / 2),
    })
  }, [bodyRef, x, y, width])

  return (
    <div
      {...useSelectHandlers(id, onSelect)}
      style={{
        ...getPositionStyles(x, y),
        width,
        height: width,
      }}
    >
      <div
        ref={usePositionedBodyRef(bodyRef, {
          width,
          height,
          xBasis: x - width / 2,
          yBasis: y - width / 2,
        })}
        css={{
          background: 'black',
        }}
        style={{
          width,
          height,
        }}
      />
    </div>
  )
}
